import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
    selector: 'app-using-documents',
    templateUrl: './using-documents.component.html',
    styleUrls: ['./using-documents.component.scss']
})
export class UsingDocumentsComponent implements OnInit {
    paramsList: Array<any> = [];

    constructor(
        public location: Location
    ) { }

    ngOnInit() {
        this.paramsList = [
            {
                name: 'outputType',
                explain: '',
                type: 'String',
                optionaValue: 'brandseriesmodel',
                defaultValue: 'model'
            },
            {
                name: 'outPutResult',
                explain: '',
                type: 'any',
                optionaValue: '-',
                defaultValue: '-'
            },
            {
                name: 'separateCharacter',
                explain: '',
                type: 'String',
                optionaValue: '',
                defaultValue: '/'
            }
        ];
    };
};



